.transactionHistory {
  width: 100%;
  height: 100%;
  padding: 25px;
  text-align: center;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 10px;
}
.thead {
  background-color: mediumaquamarine;
  color: rgb(48, 38, 38);
  height: 50px;
  text-transform: uppercase;
}
.tbody {
  color: rgb(44, 63, 87);
}
.grey {
  background-color: lightgray;
}
.white {
  background-color: #fff;
}
